@import url('https://fonts.googleapis.com/css?family=Exo');
@import url('https://fonts.googleapis.com/css?family=Righteous');
@import url('https://fonts.googleapis.com/css?family=Cuprum');
body{
	text-align: center;
	overflow-y:hidden;
	background-image:url('https://www.meme-arsenal.com/memes/38c6f553c3c49018a923514b966090a7.jpg');
	background-size:cover;
}
body>div{
	margin-top:-8px;
	width:600px;
	height:1000px;
	background-color:rgba(255,255,255,1);
	margin-left: 30%;
}
#textHolder{
	font-family: 'Exo',sans-serif;
	letter-spacing: 1px;
	color:#black;
	text-transform: uppercase;
	font-weight: bold;
	background: #b92b27;  /* fallback for old browsers */
background: -webkit-linear-gradient(to right, #1565C0, #b92b27);  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to right, #1565C0, #b92b27); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
	
	display: flex;
	flex-flow: row wrap;
	justify-content: space-around;
}
#textHolder>div>p{
	color:white;
}
#textHolder>div{
	width:47%;
	margin-bottom: 10px;
}
#textField{
	text-align: center;
	font-weight: bold;
	font-size:15px;
	height:25px;
	border:none;
	width:100%;
}
#upperText{
	position: relative;
	margin-left:100px;
	margin-top:14.5%;
	text-align: center;
 	color:white;
 	font-weight: bold;
 	font-family: 'Exo', sans-serif;
 	position: absolute;	
 	top:10px;
 	letter-spacing: 2px;
 	z-index: 10;
 	width:400px;
 	word-wrap: break-word;
}
#bottomText{
	position: relative;
	margin-left:100px;
	margin-top:40%;
	text-align: center;
 	color:white;
 	font-weight: bold;
 	font-family: 'Exo', sans-serif;
 	position: absolute;	
 	top:10px;
 	letter-spacing: 2px;
 	z-index: 10;
 	width:400px;
 	
 	word-wrap: break-word;
}
#title{
	color:#2c3e50;
	font-family: 'Righteous', cursive;
	font-size:40px;
	letter-spacing: 5px;
	position: relative;
	margin-top:0px;
}
#slogan{
	color:#34495e;
	font-family: 'Cuprum', sans-serif;
	letter-spacing: 0.3px;
	font-size:15px;
	font-weight: 500;
	text-transform: uppercase;
	position: relative;
	margin-top:-25px;
	margin-bottom:35px;
}
::placeholder{
	color:#7f8c8d;
	font-style:italic;
	font-weight: bold;
	font-size:12px;
}
#change-pic{
	border:none;
	border-radius: 10px;
	font-weight: bold;
	letter-spacing:1px;
	background: #b92b27;  /* fallback for old browsers */
background: -webkit-linear-gradient(to right, #1565C0, #b92b27);  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to right, #1565C0, #b92b27); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
	padding:10px;
	text-transform: uppercase;
	font-weight: bold;
	font-family: 'Cuprum', sans-serif;
	color:white;
	font-size:15px;
	cursor: pointer;
	transition: box-shadow 1s,font-size 1s,letter-spacing 1s;
}
#change-pic:hover{
	-webkit-box-shadow: 0px 10px 28px -8px rgba(0,0,0,0.75);
	-moz-box-shadow: 0px 10px 28px -8px rgba(0,0,0,0.75);
	box-shadow: 0px 10px 28px -8px rgba(0,0,0,0.75);
	letter-spacing: 2px;
	font-size:16px;
}